.version8 {
    .hero-section-bg {
        .vdo-section {
            height: 100vh;
            @media(max-width: 767px) {
                height:700px !important;
            }
            .hero-vdo {
                width: 100%;
                height: 100%;
                object-fit: fill;
            }
        }
        #app-owl {
            .items {
                position: relative;
                .owl-img {
                    height: 100vh;
                    filter: brightness(60%);
                    -webkit-filter: brightness(60%);
                    -moz-filter: brightness(60%);
                    -ms-filter: brightness(60%);
                    @media(max-width: 991px) {
                        width:auto !important;
                    }
                }
            }
            .owl-controls {
                .owl-dots {
                    position: absolute;
                    bottom: 50px;
                    left: 46%;
                    .owl-dot {
                        background: #6e737f;
                        width: 8px;
                        height: 8px;
                        display: inline-block;
                        margin-right: 4px;
                        margin-left: 4px;
                        border-radius: 8px;
                    }
                    .owl-dot.active {
                        background: #ffffff;
                        width: 42px;
                        height: 8px;
                        margin-right: 2px;
                        border-radius: 11px;
                    }
                }
            }
        }
        .content {
            position: absolute;
            bottom: 13%;
            left: 15%;
            z-index: 10;
            @media(max-width: 1480px) {
                left: 3%;
            }
            @media(max-width: 10240px) {
                top: 27%;
            }
            @media(max-width: 767px) {
                padding: 0 24px;
                bottom: 28%;
            }
            .hero-img8 {
                @media(max-width: 991px) {
                    display: none;
                }
                @media(max-width: 1480px) {
                    left: 15px;
                }
                img {
                    @media(max-width: 1480px) {
                        width: 100%;
                    }
                }
            }
            h1 {
                color: $black;
                @media(max-width: 991px) {
                    font-size:30px !important;
                }
            }
            p {
                color: $black;
            }
            @media(max-width:767px) {
                margin-bottom: 50px;
            }
        }
    }
}

