.version-blog {
    @media(max-width: 991px) {
        .owl-controls {
            display: none;
        }
    }
    .hero-section-blog {
        position: relative;
        .content {
            position: absolute;
            bottom: 0px;
            right: 0px;
            background: $offwhite;
            width: 100%;
            padding: 35px 40px;
            @media(max-width: 575px) {
                padding: 35px 15px;
            }
            .genric-btn3 {
                position: absolute;
                top: -50px;
                left: 0px;
            }
            h1 {
                font-size: 36px;
                font-weight: 700;
                @media(max-width: 991px) {
                    font-size: 28px;
                }
                br {
                    @media(max-width: 991px) {
                        display: none;
                    }
                }
            }
            .author {
                font-size: 12px;
                text-transform: uppercase;
            }
            .blog-summary {
                .blog-link {
                    margin-left: 24px;
                    color: $text-color;
                    font-size: 12px;
                    text-transform: uppercase;
                    @include transition();
                    &:hover {
                        color: $title-color;
                    }
                }
            }
            .blog-icon {
                @extend .d1-gradient-bg;
                color: $white;
                padding: 19px 22px;
                font-size: 18px;
            }
        }
    }
    .items {
        .owl-img {
            filter: brightness(75%);
            -webkit-filter: brightness(75%);
            -moz-filter: brightness(75%);
            @media(max-width: 991px) {
                height: 400px;
            }
        }
    }
    #blog-owl {
        .owl-nav {
            position: absolute;
            top: 50%;
            right: 0%;
            display: flex;
            width: 100%;
            justify-content: space-between;
            @media(max-width: 991px) {
                top: 560px;
                color: $white;
            }
            .owl-prev,
            .owl-next {
                @include transform(rotate(-90deg));
                i {
                    color: $white;
                    padding: 17px 18px;
                    background: rgba($white, 0.2);
                    @include transition();
                    &:hover {
                        cursor: pointer;
                        @extend .d1-gradient-bg;
                    }
                }
            }
        }
    }
    .hero-section-blog-details {
        .blog-img {
            width: 100%;
            filter: brightness(50%);
            -webkit-filter: brightness(50%);
            -moz-filter: brightness(50%);
            @media(max-width: 991px) {
                height: 400px;
            }
        }
        .content {
            width: 100%;
            position: absolute;
            bottom: 50px;
            @media(max-width: 991px) {
                bottom: 10%;
            }
            .content-wrap {
                @media(max-width: 991px) {
                    display: block;
                }
                p, div {
                    font-size: 12px;
                    text-transform: uppercase;
                }
                .content-wrap-child {
                    @media(max-width: 991px) {
                        display: block !important;
                    }
                }
                .text {
                    margin-right: 50px;
                    @media(max-width: 991px) {
                        margin-right: 0px;
                        text-align: center;
                        margin-bottom: 30px;
                    }
                }
                .icons {
                    @media(max-width: 991px) {
                        display: block !important;
                        text-align: center;
                    }
                    .icon {
                        @media(max-width: 991px) {
                            display: inline-block;
                        }
                        a {
                            color: $white;
                        }
                    }
                }
            }
            .social-link {
                margin: 0 10px;
                font-size: 14px;
                i {
                    @include transition(all .3s ease 0s);
                    color: $white;
                }
            }
            .social-link:hover {
                i {
                    color: $primary-color;
                }
            }
        }
    }
}

