    top: 105px;
    @media(max-width: 1440px) {
        width: 48%;
        top: 20px;
    }
    @media(max-width:992px) {
        display: none;
    }
}

.hero-section {
    @media(max-width: 767px) {
        padding:120px 0;
        .fullscreen {
            height: auto !important;
        }
    }
    .content-wrap {
        z-index: 2;
        h1 {
            font-size: 60px;
            font-weight: 700;
            @media(max-width: 1199px)and(min-width: 992px) {
                font-size:30px;
            }
            @media(max-width:414px) {
                font-size: 35px;
            }
        }
        p {
            padding: 12px 0px 15px;
            max-width: 510px;
            @media(max-width: 991px) {
                color:$black;
            }
        }
    }
}

.hero-img-wrap {
    position: relative;
}

